#big-description-container {
    width: auto;
    height: auto;
    flex-grow: 1;
    font-size: 6vw;
    color: white;
    font-weight: bold;
    word-break: break-all;
    white-space: normal;
    animation: fadeInAnimation ease 1s; 
    animation-iteration-count: 1; 
    animation-fill-mode: forwards;
}

#mini-description-container {
    width: auto;
    height: auto;
    flex-grow: 1;
    font-size: 2vw;
    color: white;
    font-weight: bold;
    word-break: break-all;
    white-space: normal;
    animation: fadeInAnimation ease 2s; 
    animation-iteration-count: 1; 
    animation-fill-mode: forwards;
}

#scrolldown-container {
    width: auto;
    height: auto;
    flex-grow: 1;
}

.scrolldown-label {
    font-size: 1vw;
    color: white;
}

.scrolldown {
    width: 80%;
    height: 5vh;
    border: none !important;
    background-color: rgb(255, 255, 255, 0.5);
}