#tridiv {
  perspective: 800px;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  overflow: hidden;
}

.scene,
.shape,
.face,
.face-wrapper,
.cr {
  position: absolute;
  transform-style: preserve-3d;
}
.scene {
  width: 100%;
  height: 100%;
  -webkit-transform: rotateX(-45deg) rotateY(-45deg);
  -moz-transform: rotateX(-45deg) rotateY(-45deg);
  -ms-transform: rotateX(-45deg) rotateY(-45deg);
  transform: rotateX(-45deg) rotateY(-45deg);
}
.shape {
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  transform-origin: 50%;
}
.face,
.face-wrapper {
  overflow: hidden;
  transform-origin: 0 0;
  backface-visibility: hidden;
  /* hidden by default, prevent blinking and other weird rendering glitchs */
}
.face {
  background-size: 100% 100% !important;
  background-position: center;
}
.face-wrapper .face {
  left: 100%;
  width: 100%;
  height: 100%;
}
.photon-shader {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.side {
  left: 50%;
}
.cr,
.cr .side {
  height: 100%;
}
[class*="cuboid"] .ft,
[class*="cuboid"] .bk {
  width: 100%;
  height: 100%;
}
[class*="cuboid"] .bk {
  left: 100%;
}
[class*="cuboid"] .rt {
  transform: rotateY(-90deg) translateX(-50%);
}
[class*="cuboid"] .lt {
  transform: rotateY(90deg) translateX(-50%);
}
[class*="cuboid"] .tp {
  transform: rotateX(90deg) translateY(-50%);
}
[class*="cuboid"] .bm {
  transform: rotateX(-90deg) translateY(-50%);
}
[class*="cuboid"] .lt {
  left: 100%;
}
[class*="cuboid"] .bm {
  top: 100%;
}
/* .cub-1 styles */
.cub-1 {
  transform: translate3D(0.4375em, -0.0625em, 0.0625em) rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  opacity: 1;
  width: 15em;
  height: 15em;
  margin: -7.5em 0 0 -7.5em;
}
.cub-1 .ft {
  transform: translateZ(7.5em);
}
.cub-1 .bk {
  transform: translateZ(-7.5em) rotateY(180deg);
}
.cub-1 .rt,
.cub-1 .lt {
  width: 15em;
  height: 15em;
}
.cub-1 .tp,
.cub-1 .bm {
  width: 15em;
  height: 15em;
}
.cub-1 .face {
  background-color: transparent;
}

.cub-1 .tp {
  background: linear-gradient(320deg, #0a0ac8, #fb0453, #ff9400);
  background-size: 600% 600% !important;
  -webkit-animation: gradientUp 10s ease infinite;
  -moz-animation: gradientUp 10s ease infinite;
  animation: gradientUp 10s ease infinite;
}

.cub-1 .lt {
  background: linear-gradient(320deg, #0a0ac8, #fb0453, #ff9400);
  background-size: 600% 600% !important;
  -webkit-animation: gradientDown 10s ease infinite;
  -moz-animation: gradientDown 10s ease infinite;
  animation: gradientDown 10s ease infinite;
}

@-webkit-keyframes gradientUp {
  0% {
    background-position: 15% 0%;
  }
  50% {
    background-position: 86% 100%;
  }
  100% {
    background-position: 15% 0%;
  }
}
@-moz-keyframes gradientUp {
  0% {
    background-position: 15% 0%;
  }
  50% {
    background-position: 86% 100%;
  }
  100% {
    background-position: 15% 0%;
  }
}
@keyframes gradientUp {
  0% {
    background-position: 15% 0%;
  }
  50% {
    background-position: 86% 100%;
  }
  100% {
    background-position: 15% 0%;
  }
}

@-webkit-keyframes gradientDown {
  0% {
    background-position: 13% 0%;
  }
  50% {
    background-position: 88% 100%;
  }
  100% {
    background-position: 13% 0%;
  }
}
@-moz-keyframes gradientDown {
  0% {
    background-position: 13% 0%;
  }
  50% {
    background-position: 88% 100%;
  }
  100% {
    background-position: 13% 0%;
  }
}
@keyframes gradientDown {
  0% {
    background-position: 13% 0%;
  }
  50% {
    background-position: 88% 100%;
  }
  100% {
    background-position: 13% 0%;
  }
}
